@import "../../mixins/mixin.scss";

.wraper_about{
    padding-top: 12vh;

    .about{
        position: relative;
        max-width: 1300px;
        margin: 0 auto;
        background-color: #000;

        
    
        .title_session_about{
            @include flex(flex-start, center);
            max-width: 30%;
            font-size: 34px;
            color: #fff;
            gap: 2vw;
            padding: 5vh 0 0 0;
            border-bottom: 6px solid #fff;
            font-weight: bold;
    
            
            .number_session_about{
                @include flex(center,center);
                width: 60px;
                background-color: #f5f6f7;
                color: #000;
                padding: 20px;
            }
    
        }
    
        .square{
            background-color: #f5f6f7;
            width: 30px;
            height: 30px;
        }
    
        .square_one{
            position: absolute;
            right: 0;
            top: 0;
        }
    
        .square_two{
            position: absolute;
            top: 30px;
            right: 30px;
        }
    
        .container_text_photo{
            @include flex(center,center);
            gap: 80px;
            padding: 4%;
        }
    
        .text_about{
            width: 50%;
            max-width: 700px;
            order: 1;
            
    
            .text{
                font-size: 1.5em;
                color: #f5f6f7;
            }
        }
    
        .container_photo{
            width: 50%;
            max-width: 300px;
    
            .photo{
              position: relative;
              border: 2px solid #fff;
              border-radius: 0px 50px 0px 100px;
    
              img{
                position: relative;
                top: 20px;
                left: 30px;
                max-width: 105%;
                height: 100%;
                border-radius: 0px 50px 0px 100px;
    
              }
            }
        }
    
    }
}



@media (max-width: 800px){

    .title_session_about{
        font-size: 28px !important;
        max-width: 40% !important;
    }

    .wraper_about{
        padding: 4vh 0 !important;
    }

    .container_text_photo{
        flex-wrap: wrap;
    }

    .text_about p{
        text-align: center !important;
    }

    .container_photo{
        margin: 10px 0;
    }

    .text_about{
        order: 2;
        width: 100% !important;
        max-width: 100% !important;

        .text{
            text-align: justify;
            font-size: 1.5em !important;
            color: #f5f6f7;
        }
    }

}

@media (max-width: 520px){
    .title_session_about{
        font-size: 28px !important;
        max-width: 50% !important;
    }
}

@media (max-width: 420px){
    .title_session_about{
        font-size: 26px !important;
    }
}
